.header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
  margin-top: 20px;
}

.menu{
  display: flex;
  align-items: center;
  gap: 30px;
}

.menu__item{
  display: flex;
  align-items: center;
  gap: 10px;
  line-height: 20px;
  text-decoration: none;
  
}

.menu__item:visited{
  color: var(--black);
}

.menu__counter{
font-size: 12px;
font-weight: 600;
line-height: 28px;
border: 1px solid var(--black);
border-radius: 50%;
padding: 0px 10px;

}