.search{
  display: flex;
  width: 100%;
  gap: 10px;
  margin-bottom: 30px;
}

.search__wrapper{
  position: relative;
  flex: 1;
  display: flex;
}

.search__wrapper img{
  position: absolute;
  left: 10px;
  top: 12px;
}

.search__input{
  border-radius: 5px;
  background: rgb(222, 222, 222);
  border: none;
  color: rgb(37, 37, 37);
  padding: 15px 30px 15px 50px;
  flex: 1;
}

.search__input ::placeholder{
  color: #494949;
}



.search button{
  border: none;
  background: var(--black);
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 50px;
}

